"use client"

import { useState, useCallback } from "react"
import { Button } from "@/components/ui/button"
import { Textarea } from "@/components/ui/textarea"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Checkbox } from "@/components/ui/checkbox"
import WalletConnect from "@/components/WalletConnect"

export default function Home() {
  const [noteText, setNoteText] = useState("")
  const [expiration, setExpiration] = useState("1h")
  const [password, setPassword] = useState("")
  const [confirmPassword, setConfirmPassword] = useState("")
  const [email, setEmail] = useState("")
  const [reference, setReference] = useState("")
  const [skipConfirmation, setSkipConfirmation] = useState(false)
  const [error, setError] = useState("")
  const [noteUrl, setNoteUrl] = useState("")
  const [isAuthenticated, setIsAuthenticated] = useState(false)
  const [isPremium, setIsPremium] = useState(false)
  const [isLoading, setIsLoading] = useState(false)

  const handleAuthChange = useCallback((authenticated: boolean, premium: boolean) => {
    setIsAuthenticated(authenticated)
    setIsPremium(premium)
  }, [])

  const handleCreateNote = async (e: React.FormEvent) => {
    e.preventDefault()
    setError("")
    setNoteUrl("")

    if (!noteText.trim()) {
      setError("Note text cannot be empty")
      return
    }

    if (password && password !== confirmPassword) {
      setError("Passwords do not match")
      return
    }

    try {
      setIsLoading(true)
      const response = await fetch("/api/notes", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          content: noteText,
          expirationTime: expiration,
          password: password || undefined,
          notificationEmail: email || undefined,
          reference: reference || undefined,
        }),
      })

      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.error || "Failed to create note")
      }

      setNoteUrl(data.url)
      // Clear form on success
      setNoteText("")
      setPassword("")
      setConfirmPassword("")
      setEmail("")
      setReference("")
      setSkipConfirmation(false)
    } catch (err: any) {
      setError(err.message)
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="max-w-2xl mx-auto space-y-6">
        <Card className="border-none shadow-lg">
          <CardHeader className="text-center">
            <CardTitle className="text-3xl font-bold">
              Send notes that will self-destruct after being read
            </CardTitle>
            <CardDescription className="text-lg">
              Create encrypted notes that automatically delete themselves after being read
            </CardDescription>
          </CardHeader>
        </Card>

        <WalletConnect onAuthChange={handleAuthChange} />

        {isAuthenticated && (
          <Card>
            <CardHeader>
              <CardTitle>Create a New Note</CardTitle>
              <CardDescription>
                {isPremium 
                  ? "Premium user - Unlimited notes"
                  : "Free user - Limited to 3 notes per day"}
              </CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleCreateNote} className="space-y-6">
                <div className="space-y-2">
                  <Label>Your Note</Label>
                  <Textarea
                    placeholder="Write your note here..."
                    value={noteText}
                    onChange={(e) => setNoteText(e.target.value)}
                    className={`min-h-[200px] ${isPremium ? "text-green-600" : "text-red-600"}`}
                    required
                  />
                </div>

                <div className="space-y-2">
                  <Label>Note self-destructs</Label>
                  <Select value={expiration} onValueChange={setExpiration}>
                    <SelectTrigger>
                      <SelectValue placeholder="Select expiration time" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="1h">after reading it</SelectItem>
                      <SelectItem value="24h">1 hour from now</SelectItem>
                      <SelectItem value="7d">24 hours from now</SelectItem>
                      <SelectItem value="30d">7 days from now</SelectItem>
                    </SelectContent>
                  </Select>
                </div>

                <div className="flex items-center space-x-2">
                  <Checkbox
                    id="skipConfirmation"
                    checked={skipConfirmation}
                    onCheckedChange={(checked) => setSkipConfirmation(checked as boolean)}
                  />
                  <Label htmlFor="skipConfirmation">
                    Do not ask for confirmation before showing and destroying the note
                  </Label>
                </div>

                <div className="space-y-4">
                  <Label>Manual Password (Optional)</Label>
                  <Input
                    type="password"
                    placeholder="Enter a custom password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                  />
                  {password && (
                    <Input
                      type="password"
                      placeholder="Confirm password"
                      value={confirmPassword}
                      onChange={(e) => setConfirmPassword(e.target.value)}
                    />
                  )}
                </div>

                <div className="space-y-2">
                  <Label>Destruction Notification (Optional)</Label>
                  <Input
                    type="email"
                    placeholder="Email to notify when note is destroyed"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                  />
                </div>

                <div className="space-y-2">
                  <Label>Reference Name (Optional)</Label>
                  <Input
                    placeholder="Reference name for the note"
                    value={reference}
                    onChange={(e) => setReference(e.target.value)}
                  />
                </div>

                <Button 
                  type="submit" 
                  className="w-full"
                  disabled={isLoading}
                >
                  {isLoading ? "Creating Note..." : "Create Note"}
                </Button>

                {error && (
                  <Alert variant="destructive">
                    <AlertDescription>{error}</AlertDescription>
                  </Alert>
                )}

                {noteUrl && (
                  <Alert>
                    <AlertDescription>
                      Your note is ready! Share this link:
                      <div className={`mt-2 p-2 bg-gray-50 rounded break-all ${isPremium ? "text-green-600" : "text-red-600"}`}>
                        {noteUrl}
                      </div>
                    </AlertDescription>
                  </Alert>
                )}
              </form>
            </CardContent>
          </Card>
        )}
      </div>
    </div>
  )
}
