// Types for Pi Network SDK
interface PiNetwork {
  init: (config: { version: string, sandbox?: boolean }) => void
  authenticate: (scopes: string[], onIncompletePaymentFound?: (payment: any) => void) => Promise<AuthResult>
  createPayment: (payment: PaymentData) => Promise<PaymentResult>
  openShareDialog: (title: string, message: string) => Promise<void>
}

interface AuthResult {
  user: {
    uid: string
    username: string
  }
  accessToken: string
}

interface PaymentData {
  amount: number
  memo: string
  metadata: Record<string, any>
}

interface PaymentResult {
  identifier: string
  status: string
}

declare global {
  interface Window {
    Pi?: PiNetwork
  }
}

class PiNetworkError extends Error {
  constructor(message: string) {
    super(message)
    this.name = 'PiNetworkError'
  }
}

export class PiNetworkClient {
  private static instance: PiNetworkClient
  private isAuthenticated: boolean = false
  private currentUser: AuthResult["user"] | null = null
  private accessToken: string | null = null
  private isInitialized: boolean = false

  private constructor() {}

  static getInstance(): PiNetworkClient {
    if (!PiNetworkClient.instance) {
      PiNetworkClient.instance = new PiNetworkClient()
    }
    return PiNetworkClient.instance
  }

  async init(): Promise<void> {
    try {
      if (this.isInitialized) {
        return
      }

      // Wait for SDK to be loaded
      await this.waitForSDK()

      // Initialize the SDK with sandbox mode for development
      if (window.Pi) {
        window.Pi.init({ 
          version: "2.0",
          sandbox: true // Enable sandbox mode for development
        })
        this.isInitialized = true
        console.log("Pi Network SDK initialized in sandbox mode")
      } else {
        throw new PiNetworkError("Pi Network SDK not found")
      }
    } catch (error) {
      console.error("Failed to initialize Pi Network SDK:", error)
      throw error
    }
  }

  private async waitForSDK(): Promise<void> {
    return new Promise((resolve, reject) => {
      if (typeof window !== 'undefined' && window.Pi) {
        resolve()
        return
      }

      let attempts = 0
      const maxAttempts = 50 // 5 seconds maximum wait time

      const checkSDK = setInterval(() => {
        attempts++
        if (typeof window !== 'undefined' && window.Pi) {
          clearInterval(checkSDK)
          resolve()
        } else if (attempts >= maxAttempts) {
          clearInterval(checkSDK)
          reject(new PiNetworkError("Pi Network SDK not found after timeout"))
        }
      }, 100)
    })
  }

  async authenticate(): Promise<AuthResult> {
    try {
      if (!this.isInitialized) {
        await this.init()
      }
      
      if (!window.Pi) {
        throw new PiNetworkError("Pi Network SDK not found")
      }

      console.log("Attempting authentication in sandbox mode...")
      const scopes = ["payments", "username"]
      const auth = await window.Pi.authenticate(scopes, this.onIncompletePaymentFound)
      
      if (!auth || !auth.user) {
        throw new PiNetworkError("Authentication failed")
      }

      console.log("Authentication successful:", auth)
      this.isAuthenticated = true
      this.currentUser = auth.user
      this.accessToken = auth.accessToken

      return auth
    } catch (error) {
      console.error("Authentication failed:", error)
      this.isAuthenticated = false
      this.currentUser = null
      this.accessToken = null
      throw error
    }
  }

  async createPremiumSubscription(): Promise<PaymentResult> {
    try {
      if (!this.isInitialized) {
        await this.init()
      }

      if (!window.Pi) {
        throw new PiNetworkError("Pi Network SDK not found")
      }

      if (!this.isAuthenticated) {
        throw new PiNetworkError("User not authenticated")
      }

      console.log("Creating premium subscription payment in sandbox mode...")
      const payment = await window.Pi.createPayment({
        amount: 5,
        memo: "Premium subscription - Monthly",
        metadata: {
          type: "subscription",
          plan: "premium",
          duration: "monthly",
          userId: this.currentUser?.uid,
          sandbox: true
        }
      })

      if (!payment) {
        throw new PiNetworkError("Payment creation failed")
      }

      console.log("Payment created:", payment)
      return payment
    } catch (error) {
      console.error("Payment failed:", error)
      throw error
    }
  }

  private onIncompletePaymentFound(payment: any) {
    console.log("Incomplete payment found:", payment)
    // In sandbox mode, we can simulate payment completion
    console.log("Sandbox: Simulating payment completion")
  }

  isUserAuthenticated(): boolean {
    return this.isAuthenticated && !!this.currentUser
  }

  getCurrentUser(): AuthResult["user"] | null {
    return this.currentUser
  }

  getAccessToken(): string | null {
    return this.accessToken
  }
}

export const piNetwork = PiNetworkClient.getInstance()
