"use client"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { piNetwork } from "@/lib/pi-network"

interface WalletConnectProps {
  onAuthChange?: (isAuthenticated: boolean, isPremium: boolean) => void
}

export default function WalletConnect({ onAuthChange }: WalletConnectProps) {
  const [isConnected, setIsConnected] = useState(false)
  const [isPremium, setIsPremium] = useState(false)
  const [error, setError] = useState("")
  const [isLoading, setIsLoading] = useState(false)
  const [username, setUsername] = useState("")
  const [isInitializing, setIsInitializing] = useState(true)

  // Initialize Pi Network SDK and check authentication status on mount
  useEffect(() => {
    const initPiNetwork = async () => {
      try {
        setIsInitializing(true)
        setError("")
        
        await piNetwork.init()
        
        // Check if user is already authenticated
        const user = piNetwork.getCurrentUser()
        if (user) {
          setIsConnected(true)
          setUsername(user.username)
          onAuthChange?.(true, isPremium)
        }
      } catch (err: any) {
        console.error("Failed to initialize Pi Network:", err)
        setError("Failed to initialize Pi Network. Please try again.")
      } finally {
        setIsInitializing(false)
      }
    }

    initPiNetwork()
  }, [onAuthChange, isPremium])

  const handleConnect = async () => {
    try {
      setIsLoading(true)
      setError("")

      const auth = await piNetwork.authenticate()
      
      if (!auth || !auth.user) {
        throw new Error("Authentication failed")
      }

      setIsConnected(true)
      setUsername(auth.user.username)
      onAuthChange?.(true, isPremium)

    } catch (err: any) {
      console.error("Connection error:", err)
      setError(err.message || "Failed to connect wallet. Please try again.")
      setIsConnected(false)
      onAuthChange?.(false, false)
    } finally {
      setIsLoading(false)
    }
  }

  const handleUpgrade = async () => {
    try {
      setIsLoading(true)
      setError("")

      const payment = await piNetwork.createPremiumSubscription()
      
      if (!payment) {
        throw new Error("Payment failed")
      }

      if (payment.status === "completed") {
        setIsPremium(true)
        onAuthChange?.(true, true)
      } else {
        throw new Error("Payment incomplete. Please try again.")
      }

    } catch (err: any) {
      console.error("Upgrade error:", err)
      setError(err.message || "Failed to process payment. Please try again.")
    } finally {
      setIsLoading(false)
    }
  }

  if (isInitializing) {
    return (
      <Card>
        <CardContent className="p-6">
          <div className="text-center text-gray-500">
            Initializing Pi Network...
          </div>
        </CardContent>
      </Card>
    )
  }

  if (!isConnected) {
    return (
      <Card>
        <CardHeader>
          <CardTitle>Connect Wallet</CardTitle>
          <CardDescription>
            Connect your Pi Network wallet to start creating notes
          </CardDescription>
        </CardHeader>
        <CardContent>
          <Button 
            onClick={handleConnect} 
            disabled={isLoading}
            className="w-full"
          >
            {isLoading ? "Connecting..." : "Connect Pi Wallet"}
          </Button>
          {error && (
            <Alert variant="destructive" className="mt-4">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}
        </CardContent>
      </Card>
    )
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Wallet Connected</CardTitle>
        <CardDescription>
          Welcome, {username}!
        </CardDescription>
      </CardHeader>
      <CardContent>
        <div className="space-y-4">
          <div className="flex items-center justify-between">
            <span>Account Type:</span>
            <span className={isPremium ? "text-green-600 font-medium" : "text-red-600 font-medium"}>
              {isPremium ? "Premium" : "Free"}
            </span>
          </div>

          {!isPremium && (
            <div className="space-y-2">
              <Button 
                onClick={handleUpgrade} 
                disabled={isLoading}
                className="w-full bg-green-600 hover:bg-green-700"
              >
                {isLoading ? "Processing..." : "Upgrade to Premium (5 π/month)"}
              </Button>
              <p className="text-sm text-gray-500 text-center">
                Free users are limited to 3 messages per day
              </p>
            </div>
          )}

          {error && (
            <Alert variant="destructive">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}
        </div>
      </CardContent>
    </Card>
  )
}
